.hero {
    width: 100%;
    height: 100vh;
    background: var(--darkgrey);
}
.container {
    max-width: 1240px;
    padding: 0 1rem;
    margin: auto;
}
.hero .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.col-1 {
    padding: 1rem 1rem 1rem 0;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.col-1 h1 {
    font-size: 3rem;
    font-weight: 700;
    color: var(--white);
}
.col-1 p {
    font-size: 1rem;
    color: var(--white);
    line-height: 1.4;
    margin-top: 1.2rem;
}
.col-1 .used-by {
    color: var(--lightgrey);
}
.col-1 .used-by p {
    color: var(--lightgrey);
    font-size: .7rem;
    margin: 2rem 0 1rem 0;
}
.col-2 {
    padding: 1rem;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: .8rem;
}
.col-2 p {
    color: var(--grey);
}
.form-layout {
    width: 400px;
    margin: auto;
    background-color: var(--white);
    border-radius: 6px;
}
.form-container {
    padding: 1.5rem 2rem;
}
.sign-in-txt {
    margin-bottom: 10px;
}
.social-login span {
    background: var(--white);
    padding: 16px 45px 6px 45px;
    border: 1px solid var(--lightgrey);
    border-radius: 4px;
}
.icons {
    display: flex;
}
.icons span {
    height: 30px;
    margin: 1rem;
    
}
.col-2 .divider p {
    width: 100%;
    text-align: center;
    border-bottom: 1 px solid var(--lightgrey);
    line-height: 0.1rem;
    margin: 2rem 0 2rem;
}
form input {
    display: block;
    width: 100%;
    margin: 1.2rem 0;
    padding: .5rem;
    border: 1px solid var(--lightgrey);
    background: var(--white);
    border-radius: 4px;
}
.form-footer {
    margin-top: 25px;
    border-top: 1px solid var(--lightgrey);
    background: var(--white);
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}
.form-footer p {
    padding: 1.5rem 2rem;
}
.form-footer span {
    font-weight: 500;
}
button {
    width: 100%;
    padding: 8px 0;
    border-radius: 4px;
    border: 1px solid var(--primary);
    background-color: var(--primary);
    color: var(--white);
    cursor: pointer;
}
button:hover {
    background-color: var(--secondary);
    border: 1px solid var(--secondary);
}

@media screen and (max-width:940px) {
    .hero {
        display: flex;
        flex-direction: column;
        height: auto;
        width: 100%;
    }

    .hero .container {
        padding: 0 1rem;
    }

    .hero .content {
        grid-template-columns: 1fr;
    }

    .col-1 {
        height: 94vh; 
        padding: .2rem;
        max-width: 100%;
    }

    .hero h1 {
        font-size: 2.5rem;
    }

    .hero .col-2 {
        display: none;
        margin-top: 50px;
    }
}
